const galleryContainer = document.querySelector(`.gallery`);

const createLiElement = () => {
  const liElement = document.createElement(`li`);
  liElement.classList.add(`gallery-item`);
  return liElement;
};

const createAncherElement = image => {
  const ancherElement = document.createElement(`a`);
  ancherElement.classList.add(`gallery-link`);
  ancherElement.href = image.original;
  return ancherElement;
};

const createImgElement = image => {
  const imgElement = document.createElement(`img`);
  imgElement.src = image.preview;
  imgElement.alt = image.description;
  imgElement.classList.add(`gallery-image`);
  return imgElement;
};

const galleryItems = images.map(image => {
  const liElement = createLiElement();
  const ancherElement = createAncherElement(image);
  const imgElement = createImgElement(image);

  liElement.append(ancherElement);
  ancherElement.append(imgElement);

  return liElement;
});

galleryContainer.append(...galleryItems);

new SimpleLightbox('.gallery-link', {
  captionsData: 'alt',
  captionDelay: 250,
  fadeSpeed: 1000,
});
